import React, { useState,  useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Table from 'react-bootstrap/Table';
import './Poplist.css';
import Dropdown from 'react-bootstrap/Dropdown';
import { connect } from 'react-redux';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import { deleteList } from '../actions';
import axios from 'axios';

const Poplist = ({ lists, items,deleteList }) => {


  const [sortType, setSortType] = useState(null); //   : 
  const [sortDescending, setSortDescending] = useState(true); //   : 

  const handleSortChange = (newSortType) => {
    if (newSortType === sortType) {
      setSortDescending(!sortDescending);
    } else {
      setSortType(newSortType);
      setSortDescending(true); //        
    }
  };
//     
//  
  //   
  let sortedList = [...lists.lists];
  // useEffect(() => {
  // let sortedList = [...lists.lists];
  if (sortType === 'Value') {
    sortedList = sortedList.sort((a, b) => {
      const aValue = a.List.reduce((total, item) => {
        const correspondingItem = items.items.find(i => i.Name === item.Name);
        if (correspondingItem) {
          return total + correspondingItem.YDayAvgPrice * item.Quantity / item.BundleCount;
        }
        return total;
      }, 0);

      const bValue = b.List.reduce((total, item) => {
        const correspondingItem = items.items.find(i => i.Name === item.Name);
        if (correspondingItem) {
          return total + correspondingItem.YDayAvgPrice * item.Quantity / item.BundleCount;
        }
        return total;
      }, 0);

      return sortDescending ? bValue - aValue : aValue - bValue;
    });
  } else if (sortType === 'Pop') {
    sortedList = sortedList.sort((a, b) => {
      return sortDescending ? b.Pop - a.Pop : a.Pop - b.Pop;
    });
  }
// }, [lists]);
const handleDelete = (index) => {
  const deletedItem = sortedList[index]; 
  // const deletedItem = sortedList.splice(index, 1)[0]; //   
const token = localStorage.getItem('token');
  // deleteList(deletedItem.Title);

  // deleteList(sortedList.splice(index, 1)[0].Title);
  // console.log(sortedList.splice(index, 1)[0].Title)
  //     
  axios.post(process.env.REACT_APP_BACKEND_URL +'/delete1',  { Title: deletedItem.Title },{
          headers: { Authorization: `Bearer ${token}` }})
  .then(response => {
    if (response.data === 'True') {
              //   'admin'  
              deleteList(deletedItem.Title); // Redux action   
            } else {
              console.log('Access denied: Not an admin.');
              //     
            }
  })
  .catch(error => {
    console.error('Error checking user role:', error);
    //  
  });

  
  // const updatedLists = [...sortedList];
  // sortedList=[...updatedLists.splice(index, 1)]; //    
  // console.log(sortedList)
    // Redux  
    // addList     .
    // addList(updatedLists);

  // const updatedItems = selectedItems.filter((item, i) => i !== index);
  // setSelectedItems(updatedItems);

  // setSelectedItems((prevSelectedItems) => {
  //   const updatedItems = selectedItems.filter((item, i) => i !== index);
  //   localStorage.setItem(localStorageKey, JSON.stringify(updatedItems));
  //   return updatedItems;
  // });

};

  return (
    <div className="poplist-container">
      
      <Table striped bordered hover>
        <thead>
          <tr>
            <th></th>
            <th>
              {' '}
              <span
                onClick={() => handleSortChange('Value')}
                style={{ cursor: 'pointer' }}
              >
                {sortType === 'Value' && sortDescending ? '' : ''}
              </span>
            </th>
            <th>
              {' '}
              <span
                onClick={() => handleSortChange('Pop')}
                style={{ cursor: 'pointer' }}
              >
                {sortType === 'Pop' && sortDescending ? '' : ''}
              </span>
            </th>
          </tr>
        </thead>
        <tbody>
          {sortedList.map((item, index) => (
            <tr key={index}>
              <td>{item.Title}</td>
              <td>
                {item.List.reduce((total, item) => {
                  const correspondingItem = items.items.find(i => i.Name === item.Name);
                  if (correspondingItem) {
                    return total + correspondingItem.YDayAvgPrice * item.Quantity / item.BundleCount;
                  }
                  return total;
                }, 0).toFixed(0)} G
              </td>
              <td>{item.Pop}</td>
              <td>
                <Button variant="danger" onClick={() => handleDelete(index)}></Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
      <div>    .</div>
      <div>     </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  lists: state.lists,
  items: state.items,
});

export default connect(mapStateToProps,{deleteList})(Poplist);
