//Valuelistviewer.js
import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Dropdown from 'react-bootstrap/Dropdown';
import Table from 'react-bootstrap/Table';
import './Valuelistviewer.css';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import {updateList,deleteList,addList} from '../actions';
import axios from 'axios';


import { connect } from 'react-redux';
//   
//    
//   
const Valuelistviewer = ({items,lists,addList,updateList,deleteList}) => {
  const [selectedTitle, setSelectedTitle] = useState(null);
  const [selectedListItem, setSelectedListItem] = useState(null);
  const [localStorageKey2] = useState('tableDataKey2'); //   
  const [isButtonDisabled, setButtonDisabled] = useState(false);
  const [isButtonDisabled2, setButtonDisabled2] = useState(false);
  
  useEffect(() => {
    //    
    const savedData = localStorage.getItem(localStorageKey2);
    if (savedData) {
      setSelectedListItem(JSON.parse(savedData));
    }
  }, [localStorageKey2]);
  //   


  // const totalprice = selectedListItem&&selectedListItem.List.reduce(
  //   (total, item) => total + item.YDayAvgPrice * item.Quantity / item.BundleCount,
  //   0
  // ).toFixed(0);
//selectedListItem.List  items 
const totalprice = selectedListItem && selectedListItem.List.reduce((total, item) => {
  const correspondingItem = items.items.find(i => i.Name === item.Name);
  if (correspondingItem) {
    return total + correspondingItem.YDayAvgPrice * item.Quantity / item.BundleCount;
  }return total;
}, 0).toFixed(0);



const handleDropdownSelect = (title) => {
  if (title !== selectedTitle) {
    setSelectedTitle(title);
  }
};

const handleClearTable = () => {
  setSelectedTitle(null);
  setSelectedListItem(null);
  localStorage.removeItem(localStorageKey2);
};

const handlerUpdate = () => {
  setButtonDisabled2(true);
  setTimeout(() => {
    setButtonDisabled2(false);
  }, 10000); // 3    
if(isButtonDisabled2===false){
  
  (selectedListItem&&setSelectedListItem({
    ...selectedListItem,
    Pop: selectedListItem.Pop + 1
  }));

  

  axios.post(process.env.REACT_APP_BACKEND_URL +'/popup',  selectedListItem)
  .then(response => {
    console.log('Data updated successfully:', response.data);
  })
  .catch(error => {
    console.error('Error updating data:', error);
  });

  //null reading pop 
  //store  lists 
  //selected    
  //   updatelist 

  selectedListItem&&updateList(selectedListItem);

// w  list     
}}

const handlerDelete=()=>{
  setButtonDisabled(true);
    setTimeout(() => {
      setButtonDisabled(false);
    }, 10000); // 3    
  if(isButtonDisabled===false){
  
  (selectedListItem&&setSelectedListItem({
    ...selectedListItem,
    Pop: selectedListItem.Pop - 1
  }));

  

axios.post(process.env.REACT_APP_BACKEND_URL +'/popdown',  selectedListItem)
  .then(response => {
    console.log('Data updated successfully:', response.data);
  })
  .catch(error => {
    console.error('Error updating data:', error);
  });

  selectedListItem&&updateList(selectedListItem);
}
}
useEffect(() => {
  if (selectedTitle !== null) {
    //setSelectedListItem(/(lists.lists.find(item => item.Title === selectedTitle)));

    setSelectedListItem(()=>{

      localStorage.setItem(localStorageKey2, JSON.stringify(lists.lists.find(item => item.Title === selectedTitle)))
      return lists.lists.find(item => item.Title === selectedTitle)
    });

  }
  
}, [selectedTitle, ]);

return (
  <div>
    <Dropdown className="my-dropdown">
      <Dropdown.Toggle variant="success" id="dropdown-basic">
         
      </Dropdown.Toggle>

      <Dropdown.Menu>
        {lists.lists.map((item, index) => (
          <Dropdown.Item key={index} onClick={() => handleDropdownSelect(item.Title)}>
            {item.Title}
          </Dropdown.Item>
        ))}
      </Dropdown.Menu>
    </Dropdown>

    <Form>
      <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
        <Form.Control type="price"  value={` ${selectedListItem ? selectedListItem.Title : ' '} `}
  readOnly />
  
      </Form.Group>
    </Form>
    <Table striped bordered hover>
      <thead>
        <tr>
        <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        {selectedListItem && selectedListItem.List.map((listItem, index) => (
          <tr key={index}>
                          <td>
                <img alt=""
        src={listItem.Icon} //   URL  
      /></td>
            <td>{listItem.Name}</td>
            <td>{listItem.Quantity}</td>
            <td></td>
          </tr>
        ))}
      </tbody>
    </Table>

    <Form>
      <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
        <Form.Control type="price" value={` ${selectedListItem ? `${totalprice} G` : `0 G`} `}
  readOnly />
  
      </Form.Group>
    </Form>

    <div>
      <Button variant="warning" onClick={handlerUpdate}></Button>{' '}
      <Button variant="danger" onClick={handlerDelete}></Button>{' '}
      <Button variant="dark" onClick={handleClearTable}> </Button>
      
    </div>
    <div>  30 1 .</div>
    
  </div>
);
};

const mapStateToProps = (state) => ({
  items: state.items,
  lists: state.lists,
});
export default connect(mapStateToProps,{addList,updateList,deleteList})(Valuelistviewer);

