import React,{useState} from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

function Login() {
  const navigate  = useNavigate();
  const [ID,setID]=useState('');
const [Password,setPassword]=useState('');
  const handleIDChange = (e) => {
    setID(e.target.value); //   title  
  }
  const handlePasswordChange = (e) => {
    setPassword(e.target.value); //   title  
  }
  const handleLogin = () => {
    axios.post(process.env.REACT_APP_BACKEND_URL +'/Login', { ID: ID, Password: Password })
      .then(response => {
        //       
        const token = response.data.token; //          .
        console.log(response);
        console.log(response.data);
        console.log(token);
        //     
        localStorage.setItem('token', token);
        // //     
        setID("");
        setPassword("");
        navigate ('/'); //  
        window.location.href = window.location.href;
      })
      .catch(error => {
        console.error('Error updating data:', error);
      });
  };

  return (
    <Container className="d-flex align-items-center justify-content-center" style={{ minHeight: "100vh" }}>
      <Row className="justify-content-center">
        <Col md={100}>
          <div className="p-4 border rounded">
            <Form>
            <Form.Group className="mb-3" controlId="formBasicID">
                <Form.Label>ID //   </Form.Label>
                <Form.Control type="text" placeholder="ID" onChange={handleIDChange}
            value={ID} />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label>Password</Form.Label>
                <Form.Control type="password" placeholder="Password" onChange={handlePasswordChange}
            value={Password} />
              </Form.Group>
              <Button variant="secondary" onClick={handleLogin}>
                </Button>{' '}
      <Button variant="dark"><Link to="/Signup" ></Link></Button>
            </Form>
          </div>
        </Col>
      </Row>
    </Container>
  );
}

export default Login;
